  body {
    font-family: Arial, sans-serif;
    margin: 0;
    padding: 0;
  }
  
  .container {
    max-width: 800px;
    margin: 0 auto;
    padding: 20px;
  }
  
  h1 {
    margin: 0px;
    text-align: center;
  }
  
  p {
    text-align: center;
  }
  
  form {
    display: flex;
    justify-content: center;
    margin-bottom: 20px;
  }
  
  .search-form {
    position: sticky;
    top: 0;
    background: url('https://encrypted-tbn0.gstatic.com/images?q=tbn:ANd9GcR5kDAkpom0quy_qVyBD3nSSV973yyd1hVUFHGbIZhd8w&s');
    padding: 10px;
    color: white;
    display: flex;
    justify-content: center;
    align-items: center;
    margin-bottom: 10px;
    border-bottom: 5px solid black;
  }

  .search-row {
    margin: 5px;
    margin-top: 0px;
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: center;
  }

  .search-col {
    width: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
  }

  .search-page {
    position: relative;
    background-color: rgb(221, 168, 44);
    min-height: 721px;
  }

  .load-more-button {
    position: relative;
    left: calc(50% - 50px);
    width: 100px;
    text-align: center;
    padding: 10px;
    margin-top: 20px;
    margin-bottom: 20px;
  }

  input[type="text"] {
    padding: 8px;
    font-size: 16px;
    width: 50%;
  }
  
  select {
    height: 30px;
    padding: 0px;
    margin-top: 10px;
    margin-right: 10px;
    font-size: 16px;
  }
  
  button {
    padding: 8px 16px;
    font-size: 16px;
    width: 100px;
  }
  
  .book-list {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: space-around;
  }

  .book-list-item {
    color: rgba(0, 0, 0)!important;
    background-color: rgb(198, 143, 16);
    width: 265px;
    height: auto;
    padding: 10px;
    margin-bottom: 11px;
    border: 3px solid black;
    border-radius: 5px;
    text-align: center;
  }
  
  .book-list-item a {
    color: black;
  }

  .book-list-item img {
    width: 100%;
    height: 400px;
    margin-bottom: 10px;
  }
  
  .book-detail {
    text-align: center;
  }
  
  .book-detail img {
    max-width: 200px;
    height: auto;
    margin-bottom: 10px;
  }
  
  .loading-screen {
    position: fixed;
    width: 100%;
    height: 100%;
    top: 0px;
    left: 0px;
    background-color: rgba(0, 0, 0, 0.3);
  }

  .loading-spinner {
    position: fixed;
    width: 40px;
    height: 40px;
    top: calc(50% - 20px);
    left: calc(50% - 20px);
    border: 4px solid rgba(0, 0, 0, 0.1);
    border-left-color: #fff;
    border-radius: 50%;
    animation: spin 1s linear infinite;
  }
  
  @keyframes spin {
    to {
      transform: rotate(360deg);
    }
  }

  .book-detail {
    text-align: center;
  }